<p>After we populate <code>req</code>'s form values, we can then call <code>req.FormValue()</code> to retrieve the individual values provided by the client so we can store them in variables and perform a calculation. We assume the form value keys are named <code>"operation"</code> (for the mathematical operation), <code>"left"</code> (for the left operand), and <code>"right"</code> (for the right operand); and we assume that the values of these keys are a string, a number, and another number respectively. As you can see, we're using the <code>"strconv"</code> package to attempt to convert the values returned from <code>req.FormValue("left")</code> and <code>req.FormValue("right")</code> to integers, and if the client doesn't provide the correct type of data, we return the message <code>"Unable to parse request"</code> to the client and return out of the function.</p>

