import React, { useState, useEffect } from "react";
import "./css/pages.css";
import articleContent from "../data/article_content.js";
import RelatedArticles from "../components/RelatedArticles.js";
import NotFoundPage from "./NotFoundPage";
import LikesSection from "../components/LikesSection.js";
import BackButton from "../components/BackButton.js";

export default function ArticlePage({ match, setPageTitle, setBackButton }) {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);
    const [loading, setLoading] = useState(true);
    const [articleInfo, setArticleInfo] = useState({ likes: 0 });

    useEffect(() => {
        setPageTitle(article.title);
        setBackButton(true);
    }, [article]);
    
    useEffect(() => {
        setLoading(true)
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
            setLoading(false);
        };

        fetchData();
    }, [name]);

    if (!article) return <NotFoundPage setPageTitle={setPageTitle} setBackButton={setBackButton} />;

    return (
        <>
        <section className="ArticlePage">
            <article>
                <div>
                    <div>
                        <h4>{article.title}</h4>
                        <p>
                            {article.date}
                        </p>
                    </div>
                    <LikesSection name={name} 
                                  likes={articleInfo.likes} 
                                  loading={loading} 
                                  setArticleInfo={setArticleInfo} />
                </div>
                <div dangerouslySetInnerHTML={{ __html: article.body}}></div>
            </article>
        </section>
        <BackButton />
        <footer className="ArticlePage__Footer">
            <h2>Recent Articles</h2>
            <RelatedArticles articles={articleContent.slice(0, 3)} 
                             setPageTitle={setPageTitle} 
                             setBackButton={setBackButton} />
        </footer>
        </>
    );
}