import React from "react";
import "./css/components.css";
import { Link, useLocation } from "react-router-dom";
import { MdHome, MdTagFaces, MdCode, MdGrade, MdWork } from "react-icons/md";

export default function Navbar() {
    const location = useLocation();
    const scrollToTop = () => {
        document.body.scrollTop = 0; // For Safari
        document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
    };

    return (
        <nav className="Navbar" role="navigation">
            <div>
                <div>
                    <Link to="/" className={location.pathname === "/" ? "active" : ""}  onClick={() => scrollToTop()}>
                        <MdHome />
                        Home
                    </Link>
                </div>
                <div>
                    <Link to="/about" className={location.pathname === "/about" ? "active" : ""}  onClick={() => scrollToTop()}>
                        <MdTagFaces />
                        About
                    </Link>
                </div>
                <div>
                    <Link to="/skills" className={location.pathname === "/skills" ? "active" : ""}  onClick={() => scrollToTop()}>
                        <MdCode />
                        Skills
                    </Link>
                </div>
                <div>
                    <Link to="/work" className={location.pathname === "/work" ? "active" : ""}  onClick={() => scrollToTop()}>
                        <MdWork />
                        Work
                    </Link>
                </div>
                <div>
                    <Link to="/experience" className={location.pathname === "/experience" ? "active" : ""}  onClick={() => scrollToTop()}>
                        <MdGrade />
                        Experience
                    </Link>
                </div>
            </div>
        </nav>
    );
}

