import React from "react";
import "./css/components.css";
import { Link } from "react-router-dom";
import Tag from "./Tag.js";

export default function ArticlePreview({ article }) {
    const scrollToTop = () => {
        document.body.scrollTop = 0; // For Safari
        document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
    };

    return (
        <Link to={"/article/" + article.name} onClick={() => scrollToTop()}>
            <div className="ArticlePreview to-shadow">
                <h1>{article.title}</h1>
                <p>{article.date}</p>
                <div className="tags">
                    {article.tags.map(tag => <Tag text={tag} />)}
                </div>
                <h4 dangerouslySetInnerHTML={{ __html: article.body.substring(0, 400) + "..."}}></h4>
            </div>
        </Link>
    );
}