import React, { useEffect } from "react";
import "./css/pages.css";
import ArticlePreview from "../components/ArticlePreview.js";
import articleContent from "../data/article_content.js";

export default function ArticleListPage({ setPageTitle, setBackButton }) {
    useEffect(() => {
        setPageTitle("Tye Porter");
        setBackButton(false);
    }, []);

    return (
        <section className="ArticleListPage">
            <ul>
                {articleContent.map((article, k) => {
                   return (
                        <li>
                            <ArticlePreview key={"art-prev-" + k} article={article} />
                        </li>
                   )
                })}
            </ul>
        </section>
    );
}