import React, { useEffect, useState } from "react";
import './App.css';
import {
	Route,
	Switch,
} from "react-router-dom"
import Navbar from "./components/Navbar.js";
import ArticleListPage from "./pages/ArticleListPage.js";
import AboutPage from "./pages/AboutPage.js";
import ExperiencePage from "./pages/ExperiencePage.js";
import WorkPage from "./pages/WorkPage.js";
import SkillsPage from "./pages/SkillsPage.js";
import ArticlePage from "./pages/ArticlePage.js";
import NotFoundPage from "./pages/NotFoundPage";

function App() {
	let [pageTitle, setPageTitle] = useState("");
	let [backButton, setBackButton] = useState(false);

	return (
		<div className="App">
			<header>
				<h1 className={(backButton ? "article-title" : "")}>{pageTitle}</h1>
				<Navbar />
			</header>
			<main>
				<hr />
				<Switch>
					<Route path="/" exact>
						<ArticleListPage setPageTitle={setPageTitle} setBackButton={setBackButton} />
					</Route>
					<Route path="/about">
						<AboutPage setPageTitle={setPageTitle} setBackButton={setBackButton} />
					</Route>
					<Route path="/experience">
						<ExperiencePage setPageTitle={setPageTitle} setBackButton={setBackButton} />
					</Route>
					<Route path="/work">
						<WorkPage setPageTitle={setPageTitle} setBackButton={setBackButton} />
					</Route>
					<Route path="/skills">
						<SkillsPage setPageTitle={setPageTitle} setBackButton={setBackButton} />
					</Route>
					<Route path="/article/:name" children={({match}) => 
						(<ArticlePage match={match} setPageTitle={setPageTitle} setBackButton={setBackButton} />)
					} />
					<Route>
						<NotFoundPage setPageTitle={setPageTitle} setBackButton={setBackButton} />
					</Route>
				</Switch>
			</main>
		</div>
	);
}

export default App;
