import React, { useEffect } from "react";

export default function NotFoundPage({ setPageTitle, setBackButton}) {
	useEffect(() => {
        setPageTitle("404 Page Not Found");
        setBackButton(false);
    }, []);

    return (
        <div className="NotFoundPage">
            <h1>404</h1>
            <h3>Page Not Found</h3>
        </div>
    );
}