import React from "react";
import "./css/components.css";
import { MdThumbUp } from "react-icons/md";

export default function LikesSection({ name, likes, loading, setArticleInfo }) {
	const likeArticle = async () => {
		const result = await fetch(`/api/articles/${name}/like`, {
			method: "POST",
		});
		const body = await result.json();
		setArticleInfo(body);
	};

	return (
		<button className="LikesSection" onClick={() => likeArticle()}>
			<p><MdThumbUp /></p>
			<span>{loading ? "0" : likes}</span>
		</button>
	);
}
